.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-fade infinite 2s alternate;
          animation: App-logo-fade infinite 2s alternate;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 180px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-fade {
  from {
    opacity: 0.5;
  }
  to {
    opacity: 1;
  }
}

@keyframes App-logo-fade {
  from {
    opacity: 0.5;
  }
  to {
    opacity: 1;
  }
}



